.bus-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    overflow-x: auto;
    padding: 1rem;
    box-sizing: border-box;
  }
  
  .row {
    display: flex;
    justify-content: center;
    margin-bottom: 0.4rem;
    gap: 0.3rem;
    flex-wrap: nowrap;
  }
  
  .seat {
    width: 36px;
    height: 36px;
    border-radius: 5px;
    border: 2px solid #333;
    background-color: #4ade80; /* green */
    color: #000;
    font-weight: bold;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    user-select: none;
    transition: all 0.2s;
  }
  
  .seat:hover {
    transform: scale(1.05);
  }
  
  .seat.occupied {
    background-color: #f87171; /* red */
    cursor: not-allowed;
    color: #fff;
  }
  
  .seat.selected {
    background-color: #3b82f6; /* blue */
    color: #fff;
  }
  
  .driver-row,
  .guide-row {
    margin-bottom: 1rem;
  }
  
  .driver {
    background-color: #e5e7eb; /* gray */
    cursor: default;
  }
  
  .aisle {
    width: 20px;
  }
  
  .back-row {
    margin-top: 1rem;
  }
  
  .selected-info {
    font-size: 14px;
    margin-top: 1rem;
    text-align: center;
  }
  
  /* Секция скролла */
  .bus-container::-webkit-scrollbar {
    height: 4px;
  }
  
  .bus-container::-webkit-scrollbar-thumb {
    background-color: #aaa;
    border-radius: 2px;
  }
  